html {
  scroll-behavior: smooth;
}

body {
  letter-spacing: .03em;
  min-width: 320px;
  cursor: default;
  font-family: DM Sans, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  text-decoration: none;
  list-style: none;
}

img {
  height: auto;
  display: block;
}

.hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header {
  width: 100%;
  z-index: 100;
  padding-top: 8px;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
}

@media (min-width: 768px) {
  .header {
    padding-top: 32px;
    padding-bottom: 32px;
  }
}

@media (min-width: 1200px) {
  .header {
    margin-left: 0;
    padding-top: 37px;
    padding-bottom: 37px;
  }
}

.header__container {
  align-items: center;
  display: flex;
}

@media (min-width: 1200px) {
  .header__container {
    justify-content: space-between;
  }
}

.header-button--main {
  display: none;
}

@media (min-width: 768px) {
