@media screen and (max-width: 768px) {
    .about {
      background: #fff5f6;
      padding: 0 20px;
    }
    .about-title {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 60px;
      padding-left: 64px;
      padding-right: 56px;
      &__second {
        color: #ffa5ba;
        width: 198px;
        height: 21px;
        text-transform: uppercase;
        text-align: center;
        font-weight: 400;
        font-size: 18px;
        line-height: 21px;
        font-family: 'Titan One';
        font-style: normal;
      }
      &__first {
        font-family: 'Titan One';
        font-style: normal;
        font-weight: 400;
        font-size: 34px;
        line-height: 39px;
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        margin-top: 20px;
        color: #d41443;
      }

      
    }
    .container-about {
    }
  
    .about-description {
      background-image: url(../images/about-bg-M@X1.png);
      background-repeat: no-repeat;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 18px;
      &__first {
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 162%;
        letter-spacing: 0.04em;
        color: #000000;
        width: 220px;
        height: 111px;
      }
  
      &__second {
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 162%;
        letter-spacing: 0.04em;
        color: #907e82;
        width: 220px;
        height: 171px;
      }
    }
    .about-button {
      display: flex;
      justify-content: center;
      font-family: 'DM Sans';
      font-style: normal;
      font-weight: 700;
      font-size: 14px;
      line-height: 18px;
      display: flex;
      align-items: center;
      text-align: center;
      color: #d41443;
      background: #ffffff;
      border-radius: 22px;
      width: 176px;
      height: 44px;
      margin-top: 20px;
      margin-bottom: 40px;
      &__icon {
        margin-left: 14px;
      }
    }
    .about-button:hover,
    .about-button:focus {
      background: #ecebeb;
      border-radius: 22px;
    }

    .advantages-list {
      display: flex;
      flex-direction: column;
      gap: 32px;
      &__item {
        display: flex;
        flex-direction: column;
        gap: 16px;
        align-items: center;
      }
  
      &__number {
        font-family: 'Titan One';
        font-style: normal;
        font-weight: 400;
        font-size: 36px;
        line-height: 41px;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #d41443;
        &__text {
          font-family: 'DM Sans';
          font-style: normal;
          font-weight: 700;
          font-size: 12px;
          line-height: 170%;
          letter-spacing: 0.02em;
          color: #000000;
        }
      }
    }
  }
  
